.test1 {
  width: 10.3125rem;
  height: 5.3125rem;
  font-size: 1rem;
  position: relative;
  background: #eee;
}
.test1:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 1px;
  border: 1px solid red;
  color: red;
  height: 200%;
  transform-origin: left top;
  transform: scale(0.5);
  border-radius: 0;
}
.mobile-home-title {
  font-size: 1.125rem;
  padding: 14px 24px 14px 24px;
  padding-left: 0.75rem;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  top: 0;
  z-index: 999;
  color: #fff;
  position: relative;
}
.mobile-home-title span {
  position: absolute;
  left: 0.3125rem;
  min-width: 2.71875rem;
  min-height: 1.875rem;
  text-align: center;
}
.mobile-home-title .active {
  display: none;
}
.isBackground {
  background-color: #13141c;
}
.noBackground {
  background-color: #1c1c1f;
}
